/* http://htmlcolorcodes.com/ */

.ProgressOuter {
  width: 100%;
  height: 20px;
  border-radius: 5px;
  background: -webkit-repeating-linear-gradient(45deg,
    #999,
    #999 10px,
    #777 10px,
    #777 20px);
  background: -o-repeating-linear-gradient(45deg,
    #999,
    #999 10px,
    #777 10px,
    #777 20px);
  background: repeating-linear-gradient(45deg,
    #999,
    #999 10px,
    #777 10px,
    #777 20px);
}

.ProgressInner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;

  height: 100%;
  border-radius: 5px 0 0 5px;
}

.ProgressInner.blue {
  background: -webkit-repeating-linear-gradient(45deg,
    #3498DB,
    #3498DB 10px,
    #2E86C1 10px,
    #2E86C1 20px);
  background: -o-repeating-linear-gradient(45deg,
    #3498DB,
    #3498DB 10px,
    #2E86C1 10px,
    #2E86C1 20px);
  background: repeating-linear-gradient(45deg,
    #3498DB,
    #3498DB 10px,
    #2E86C1 10px,
    #2E86C1 20px);
}

.ProgressInner.red {
  background: -webkit-repeating-linear-gradient(45deg,
    #E74C3C,
    #E74C3C 10px,
    #CB4335 10px,
    #CB4335 20px);
  background: -o-repeating-linear-gradient(45deg,
    #E74C3C,
    #E74C3C 10px,
    #CB4335 10px,
    #CB4335 20px);
  background: repeating-linear-gradient(45deg,
    #E74C3C,
    #E74C3C 10px,
    #CB4335 10px,
    #CB4335 20px);
}



