@-webkit-keyframes CarouselSlideInFromLeft {
  0%   { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
  100% { -webkit-transform: translateX(0); transform: translateX(0);     }
}
@keyframes CarouselSlideInFromLeft {
  0%   { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
  100% { -webkit-transform: translateX(0); transform: translateX(0);     }
}
@-webkit-keyframes CarouselSlideOutToLeft {
  0%   { -webkit-transform: translateX(0); transform: translateX(0);     }
  100% { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
}
@keyframes CarouselSlideOutToLeft {
  0%   { -webkit-transform: translateX(0); transform: translateX(0);     }
  100% { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
}
@-webkit-keyframes CarouselSlideInFromRight {
  0%   { -webkit-transform: translateX(100%); transform: translateX(100%);  }
  100% { -webkit-transform: translateX(0); transform: translateX(0);     }
}
@keyframes CarouselSlideInFromRight {
  0%   { -webkit-transform: translateX(100%); transform: translateX(100%);  }
  100% { -webkit-transform: translateX(0); transform: translateX(0);     }
}
@-webkit-keyframes CarouselSlideOutToRight {
  0%   { -webkit-transform: translateX(0); transform: translateX(0);    }
  100% { -webkit-transform: translateX(100%); transform: translateX(100%); }
}
@keyframes CarouselSlideOutToRight {
  0%   { -webkit-transform: translateX(0); transform: translateX(0);    }
  100% { -webkit-transform: translateX(100%); transform: translateX(100%); }
}

.Carousel {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}

.Carousel .Arrow {
  font-weight: 900;
  font-size: 60px;
  vertical-align: middle;
  position: absolute;
  text-shadow: 1px 1px 0px #FFF;
}

.Carousel .Arrow.Left {
  left: 5px;
}

.Carousel .Arrow.Right {
  right: 5px;
}


.Carousel .Arrow.disabled {
  pointer-events: none;
  cursor: default;
  opacity: 0.6;
}

.Carousel .Slide {
  position: absolute;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center; /* align horizontal */
  -ms-flex-align: center;
      align-items: center;
}

.Carousel .Slide.InFromLeft {
  -webkit-animation: 0.6s linear 0s 1 normal both CarouselSlideInFromLeft;
          animation: 0.6s linear 0s 1 normal both CarouselSlideInFromLeft;
}
.Carousel .Slide.OutToLeft {
  -webkit-animation: 0.6s linear 0s 1 normal both CarouselSlideOutToLeft;
          animation: 0.6s linear 0s 1 normal both CarouselSlideOutToLeft;
}
.Carousel .Slide.InFromRight {
  -webkit-animation: 0.6s linear 0s 1 normal both CarouselSlideInFromRight;
          animation: 0.6s linear 0s 1 normal both CarouselSlideInFromRight;
}
.Carousel .Slide.OutToRight {
  -webkit-animation: 0.6s linear 0s 1 normal both CarouselSlideOutToRight;
          animation: 0.6s linear 0s 1 normal both CarouselSlideOutToRight;
}

.Carousel .Slide > p{
  font-size: 80px;
  font-weight: 500;
}

.Carousel .Slide > img{
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}



